import FileItem from "jx/editor/editionitems/FileItem";
export default class PDF extends FileItem {
    constructor(dataObject) {
        dataObject.data.asset.type = "PSD";
        super(Object.assign({
            icoSymbol: "file-powerpoint-o",
            actions: ["download", "preview"],
            informations: ["asset"],
            backgroundColor: "#fff9ba"
        }, dataObject));
    }
}
